
import { defineComponent, onMounted, ref } from "vue";
import { useRouter, useRoute } from "vue-router";
import IConcessionary from "@/store/modules/concessionary/interface/concessionary.interface"
import ISite from "@/store/modules/site/interface/site.interface"
import ConcessionaryService from "@/store/modules/concessionary/service/concessionary.service";
import SiteService from "@/store/modules/site/service/site.service";
import SlideService from "@/store/modules/slide/service/slide.service";
import ISlide from "@/store/modules/slide/interface/slide.interface";
import Swal from "sweetalert2/dist/sweetalert2.js";
import Multiselect from '@vueform/multiselect';
import * as validationHelper from "@/store/helpers/validation.helper";

export default defineComponent({
  name: "SlidesCreate",
  components: {
	  Multiselect
  },
  setup() {
    	const router = useRouter();
		const show = ref<Boolean>(false);

		const example4 = ref({
			mode: "tags",
			value: ["batman"],
			options: [
				{ value: "batman", label: "Batman" },
				{ value: "robin", label: "Robin" },
				{ value: "joker", label: "Joker" }
			],
			searchable: true,
			createTag: true
			});

		const concessionaryService = new ConcessionaryService();
		const siteService = new SiteService();
		const slideService = new SlideService();

		const concessionaries = ref<Array<IConcessionary>>([]);
		const sites = ref<Array<ISite>>([]);
		let file : any = null;

		const slide = ref<ISlide>({
			id: null,
			name: '',
			file: null,
			concessionary_id: null,
			site_id: null,
			visible: false,
			position: '',
			editable: false,
			duration: 0,
			quantity: null,
			date_end: null,
			type: '',
			title: ''
		});

		const getConcessionaries = (sedeId) => concessionaryService.getDataBySede(sedeId)
		.then((response) => {
			concessionaries.value = [...[{id: null, name: 'Todos los concesionarios'}], ...response.data]
		})
		.catch((error) => {
			
		});

		const getSites = () => siteService.getData()
		.then((response) => {
			sites.value = [...[{id: null, name: 'Todas las sedes'}], ...response.data]
		})
		.catch((error) => {
			
		});

		const validateForm = () => {
			let disabled = false;
			if(slide.value.name.length == 0 || !file || isNaN(slide.value.duration) || !slide.value.duration || !slide.value.position || slide.value.position == '') disabled = true;
			return disabled
		}

		const changeFile = (e: any) => {
          if(e.target.files.length > 0){
            file = e.target.files[0]
          }
        }

		const saveData = () => {
			show.value = true;
			Swal.fire({
				title: 'Confirmación!',
				text: "¿Esta seguro que desea guardar los datos?",
				icon: 'question',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Si, Guardar',
				cancelButtonText: 'No, Cancelar',
				reverseButtons: true
				}).then((result) => {
					if (result.isConfirmed) {
						slideService.create(groupData())
						.then((response) => {
							Swal.fire(
							'Confirmación!',
							'Información guardada exitosamente',
							'success'
							).then(result => {
								router.push({ name: "slides" });
							});
							show.value = false;
						})
						.catch((error) => {
							Swal.fire(
							'Oh no!',
							'No se guardaron los datos',
							'error'
							);
							show.value = false;
						});
					}
			})
			
		}

		const groupData = () => {
			let formData = new FormData();
			formData.append('file', file);
			formData.append('name', slide.value.name);
			formData.append('visible', slide.value.visible?'1':'0');
			formData.append('duration', slide.value.duration.toString());
			formData.append('position', slide.value.position.toString());
			formData.append('concessionary_id', slide.value.concessionary_id ? slide.value.concessionary_id.toString():'');
			formData.append('site_id', slide.value.site_id ? slide.value.site_id.toString():'');
			return formData;
		}

		const changeSite = (e) => {
			if(e && e != ''){
				let siteName = ''
				sites.value.forEach(site =>{
					if(site.id == e){
						siteName = site.name
					}
				})
				getConcessionaries(siteName)
			}
			else{
				concessionaries.value = []
				slide.value.concessionary_id = null
				slide.value.site_id = null
			}
		}

		onMounted(() => {
			getSites()
		});

		return {
            concessionaries,
			sites,
			getConcessionaries,
			getSites,
			validateForm,
			changeFile,
			saveData,
			changeSite,
			slide,
			show,
			validationHelper
        };
  },
});
