import ApiService from "@/core/services/ApiService";
import ISlide from "@/store/modules/slide/interface/slide.interface"; 
import JwtService from "@/core/services/JwtService";
import axios from 'axios';
import {httpOptionsFile, httpOptions} from '../../global';

const apiServie = new ApiService();

export default class SlideService{
  getData() {
    return axios.get(`${ApiService.vueInstance.axios.defaults.baseURL}/slide`, httpOptions());
  }

  edit(id: any) {
    return axios.get(`${ApiService.vueInstance.axios.defaults.baseURL}/slide/${id}`, httpOptions());
  }

  create(data: any) {
    return axios.post(`${ApiService.vueInstance.axios.defaults.baseURL}/slide`, data, httpOptionsFile());
  }

  update(id: any, data: any) {
    return axios.patch(`${ApiService.vueInstance.axios.defaults.baseURL}/slide/${id}`, data, httpOptionsFile());
  }

  updateOrder(data: any) {
    return axios.post(`${ApiService.vueInstance.axios.defaults.baseURL}/slide/update-order`, data, httpOptions());
  }

  saveAnimation(id: any, data: any) {
    return axios.patch(`${ApiService.vueInstance.axios.defaults.baseURL}/slide/${id}/animation`, data, httpOptions());
  }

  destroy(id:any) {
    return axios.delete(`${ApiService.vueInstance.axios.defaults.baseURL}/slide/${id}`, httpOptions());
  }

  changeStatus(id: any, data: any) {
    return axios.patch(`${ApiService.vueInstance.axios.defaults.baseURL}/slide/${id}/status`, data, httpOptions());
  }

  saveFiles(data: any) {
    return axios.post(`${ApiService.vueInstance.axios.defaults.baseURL}/slide/pozos/files`, data, httpOptionsFile());
  }
}
